/* =====================
    Blog scss 
 ========================== */
.blog-box {
    .blog-image {
        border-radius: 5px;
    }

    .blog-detail {
        margin-top: 15px;
        display: block;

        &.blog-contain {
            background-color: $danger-color;
        }

        h6 {
            color: $content-color;
            margin-bottom: 3px;
            font-weight: 600;
        }

        h5 {
            color: $title-color;
            line-height: 1.5;
            font-weight: 600;
            font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
        }
    }
}

.blog-section {
    .blog-box {
        padding-bottom: 1px;
        border-radius: 10px;
        border: 1px solid $border-color;
        overflow: hidden;
        position: relative;

        .blog-image {
            position: relative;

            label {
                position: absolute;
                bottom: 10px;
                left: 10px;
                background-color: var(--theme-color);
                color: $white;
                border-radius: 5px;
                border: none;
                padding: 8px 10px;
                font-size: 12px;
                letter-spacing: 0.9px;
                box-shadow: 3px 3px 6px rgba($black, 0.2);
            }
        }

        .blog-detail {
            padding: 20px;
            margin-top: 0;

            h2 {
                font-size: 22px;
                line-height: 1.4;
            }

            label {
                font-size: 12px;
                padding: 7px 11px;
                border-radius: 50px;
                background-color: #0a7e1d1a;
                color: #0a7e1d;
                margin-bottom: 12px;
            }

            a {
                color: $title-color;
            }

            h3 {
                line-height: 1.4;
                margin-bottom: 5px;
                font-weight: 600;
            }

            h5 {
                font-size: 15px;
                margin-top: 10px;
                font-weight: 500;
                color: $content-color;
            }

            .blog-list {
                @include flex_common ($dis: flex, $align: center, $justify: flex-start);
                margin-top: 15px;
                gap: calc(14px + (22 - 14) * ((100vw - 320px) / (1920 - 320)));

                span {
                    color: $content-color;
                    margin-bottom: -5px;
                }

                .blog-social-icon {
                    position: relative;

                    .icon {
                        position: absolute;
                        top: 0;
                        right: 0;
                        display: flex;
                        align-items: center;
                        background-color: $white;
                        transition: all .3s ease-in-out;
                        opacity: 0;

                        li {
                            transition: all .3s ease-in-out;
                            @include flex_common;
                            @include pseudowh($width: 20px, $height: 20px);

                            .fab {
                                font-size: 14px;
                                color: $content-color;
                            }

                        }
                    }

                    &:hover {
                        .icon {
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }
}