/**=====================
     Offer scss
==========================**/
.offer-box {
    position: relative;
    z-index: 0;

    .offer-contain {
        @include flex_common;
        @include pseudowh;

        @include mq-max(lg) {
            display: block;
            text-align: center;
        }

        &::before {
            @include mq-max(lg) {
                @include pos;
                @include pseudowh;
                top: 0;
                left: 0;
                background-color: rgba($white, 0.22);
                z-index: -1;
            }
        }

        .offer-detail {
            h2 {
                text-transform: none;
                line-height: calc(27px + (43 - 27) * ((100vw - 320px) / (1920 - 320)));
                font-weight: 400;
            }

            p {
                font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                margin-top: calc(4px + (12 - 4) * ((100vw - 320px) / (1920 - 320)));
            }
        }

        .offer-timing {
            margin-left: 24px;

            @include mq-max(2xs) {
                margin-left: 0;
            }

            .time {
                ul {
                    li {
                        position: relative;

                        +li {
                            margin-left: 16px;

                            [dir="rtl"] & {
                                margin-left: unset;
                                margin-right: 16px;
                            }

                            &::before {
                                @include pos($pos: absolute, $content: ":");
                                @include center(vertical);
                                left: -12px;
                                font-size: 20px;

                                [dir="rtl"] & {
                                    left: unset;
                                    right: -12px;
                                }
                            }
                        }

                        .counter {
                            background-color: $danger-color;
                            color: $white;
                            border-radius: 5px;

                            div {
                                @include pseudowh($width: calc(46px + (61 - 46) * ((100vw - 320px) / (1920 - 320))), $height: calc(50px + (73 - 50) * ((100vw - 320px) / (1920 - 320))));
                                @include flex_common;

                                h3 {
                                    font-size: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
                                    font-weight: 600;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.offer-section {
    .offer-box {
        @include flex_common;
        background-image: url(../images/grocery/celebration.png);
        background-repeat: repeat;
        overflow: hidden;
        border: 4px dashed var(--theme-color);
        padding: 20px;
        position: relative;
        border-radius: 6px;

        @include mq-max(sm) {
            display: block;
            text-align: center;
        }

        &::before {
            @include pos;
            @include pseudowh;
            top: 0;
            left: 0;
            background-color: var(--theme-color);
            opacity: .1;
            z-index: -1;
        }

        h2 {
            margin-left: 20px;
            color: var(--theme-color);
            font-weight: 500;
            font-size: calc(18px + (22 - 18) * ((100vw - 320px) / (1920 - 320)));

            @include mq-max(sm) {
                margin-left: 0;
                line-height: 1.3;
                margin-top: 10px;
            }

            span {
                font-weight: 700;
                font-size: calc(24px + (28 - 24) * ((100vw - 320px) / (1920 - 320)));
            }
        }
    }
}